
@font-face {
  font-family: 'sell-icon';
  src: url("/../fonts/sell-icon.eot?nowozp");
  src: url("/../fonts/sell-icon.eot?nowozp#iefix") format('embedded-opentype'), url("/../fonts/sell-icon.ttf?nowozp") format('truetype'), url("/../fonts/sell-icon.woff?nowozp") format('woff'), url("/../fonts/sell-icon.svg?nowozp#sell-icon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-arrow_lift:before {
  content: "\e900";
}
.icon-check_circle:before {
  content: "\e901";
}
.icon-close:before {
  content: "\e902";
}
.icon-favorite:before {
  content: "\e903";
}
.icon-keyboard_arrow_right:before {
  content: "\e904";
}
.icon-remove_circle_outline:before {
  content: "\e905";
}
.icon-shopping_cart:before {
  content: "\e906";
}
.icon-thumb_down:before {
  content: "\e907";
}
.icon-thumb_up:before {
  content: "\e908";
}
.icon-add_circle:before {
  content: "\1f4";
}
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHetiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.boder-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.boder-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.detailWrapper {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.detailWrapper.move-enter-avtive,
.detailWrapper.move-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.detailWrapper.move-enter,
.detailWrapper.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.detailWrapper .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.detailWrapper .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.detailWrapper .image-header .back {
  position: absolute;
  top: 10px;
  left: 10px;
}
.detailWrapper .image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.detailWrapper .content {
  position: relative;
  padding: 18px;
}
.detailWrapper .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.detailWrapper .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 0;
}
.detailWrapper .content .detail .sell-count,
.detailWrapper .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.detailWrapper .content .detail .sell-count {
  margin-right: 12px;
}
.detailWrapper .content .price {
  font-weight: 700;
  line-height: 24px;
}
.detailWrapper .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.detailWrapper .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.detailWrapper .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.detailWrapper .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  border-radius: 12px;
  font-size: 10px;
  color: #fff;
  background: #00a0dc;
}
.detailWrapper .info {
  padding: 18px;
}
.detailWrapper .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  font-weight: 500;
  color: #07111b;
}
.detailWrapper .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
}
.detailWrapper .rating {
  padding: 18px 0;
  position: relative;
}
.detailWrapper .rating .title {
  padding-left: 18px;
  font-size: 14px;
  font-weight: 500;
  color: #07111b;
}
.detailWrapper .rating .ratingselect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  font-size: 0;
}
.detailWrapper .rating .ratingselect .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  line-height: 16px;
  font-size: 12px;
  color: #4d555d;
}
.detailWrapper .rating .ratingselect .rating-type .block.active {
  color: #fff;
}
.detailWrapper .rating .ratingselect .rating-type .block .count {
  font-size: 8px;
  margin-left: 2px;
}
.detailWrapper .rating .ratingselect .rating-type .block.positive {
  background: rgba(0,160,220,0.2);
}
.detailWrapper .rating .ratingselect .rating-type .block.positive.active {
  background: #00a0dc;
}
.detailWrapper .rating .ratingselect .rating-type .block.negative {
  background: rgba(77,85,93,0.2);
}
.detailWrapper .rating .ratingselect .rating-type .block.negative.active {
  background: #4d555d;
}
.detailWrapper .rating .ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
  font-size: 0;
}
.detailWrapper .rating .ratingselect .switch.on .icon-check_circle {
  color: #00c850;
}
.detailWrapper .rating .ratingselect .switch .icon-check_circle {
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  font-size: 24px;
}
.detailWrapper .rating .ratingselect .switch .text {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}
.detailWrapper .rating .ratingselect .rating-wrapper {
  margin: 0 18px;
}
.detailWrapper .rating .ratingselect .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.detailWrapper .rating .ratingselect .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.detailWrapper .rating .ratingselect .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
  font-size: 10px;
  color: #93999f;
}
.detailWrapper .rating .ratingselect .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.detailWrapper .rating .ratingselect .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.detailWrapper .rating .ratingselect .rating-wrapper .rating-item .text {
  font-size: 12px;
  color: #07111b;
  line-height: 16px;
}


